export class  User {
  private _id: number;
  private _alias: string;

  constructor(id: number, alias: string) {
    this._id = id;
    this._alias = alias;
  }


  get id(): number {
    return this._id;
  }

  set id(value: number) {
    this._id = value;
  }

  get alias(): string {
    return this._alias;
  }

  set alias(value: string) {
    this._alias = value;
  }
}
